  methods: {
    onSenderAddressStateCodeChange() {
      for(var senderStateCount = 0; senderStateCount < this.senderAddressStateItems.length; senderStateCount++) {
        if(this.orderDetail.senderAddressStateCode == this.senderAddressStateItems[senderStateCount].value) {
          this.orderDetail.senderAddressState = this.senderAddressStateItems[senderStateCount].name;
        }
      }
    },
    onRecipientAddressStateCodeChange() {
      for(var recipientStateCount = 0; recipientStateCount < this.recipientAddressStateItems.length; recipientStateCount++) {
        if(this.orderDetail.recipientAddressStateCode == this.recipientAddressStateItems[recipientStateCount].value) {
          this.orderDetail.recipientAddressState = this.recipientAddressStateItems[recipientStateCount].name;
        }
      }
    },
    setAllCheck(e) {
    var checked = e.target.checked;
    this.selected = [];
    $('input[name="item"]').prop('checked', checked)
      if(checked) {
        for(var count=0; count < this.desserts.length; count++) {
          this.selected.push(this.desserts[count]);
        }
      }
    },
    setCheck(item, checked) {
      if(checked) {
        this.selected.push(item);
      } else {
        const idx = this.selected.indexOf(item)
        if (idx > -1) { this.selected.splice(idx, 1) }
      }
    },    
    keyEnter(value) {
      if (value.code == "Enter") {
        this.getDataInit();
      }
    },
    getMakeExcel() {
      this.selectedTasks = this.selected;
      if(this.selectedTasks != "") {
        var excelDataList = [];
        excelDataList.push(["GEM Master No", "Marketplace Platform", "Store Name", "Order No", "Order Date", "Shipping Term", "Sender Name", "Sender Company"
        , "Sender Email", "Sender Phone No", "Sender Mobile No", "Sender Street1", "Sender Street2", "Sender City", "Sender State Province", "Sender Post Code", "Sender Country Code"
        , "Recipient Name", "Recipient Local Name", "Recipient Company", "Recipient Email", "Recipient Phone No", "Recipient Mobile No", "Recipient Tax ID", "Recipient Street1", "Recipient Street2"
        , "Recipient City", "Recipient State Province", "Recipient Post Code", "Recipient Country Code", "Shipment Actual Weight", "Shipment Vol Weight", "Weight Unit", "Width(cm)", "Length(cm)", "Height(cm)"
        , "Package Type", "Parcel Count", "Service Type", "Contents Type", "Item Name", "Item Quantity", "Item Value", "Item Value Currency", "Item Weight(kg)", "Item HS Code", "Item Orgin", "Item Sales URL"
        ,"Item Barcode No", "Item SKU", "Order Deleted"]);

        for(var orderCount=0;orderCount<this.selectedTasks.length;orderCount++) {
