    setDelete() {
      this.selectedTasks = this.selected;
      if (this.selectedTasks.length == 0) {
        this.$alert(common.messages.orderSelect, "", "info").then(() => {});
        return false;
      } else {
          this.$confirm(common.messages.orderDelete, "", "question").then(() => {
          this.setLoadingBar.loadingBarDialogShow = true;
          var loginId = this.$session.get("loginId");
          var authorization = this.$session.get("authorization");
          var vm = this;

          var axios = require("axios");
          var dataList = [];
          for(var orderCount=0; orderCount<this.selectedTasks.length; orderCount++) {
            var data = new Object();  
            data.deletedBy = loginId,
            data.id = this.selectedTasks[orderCount].id,
            
            dataList.push(data);
          }

          var config = {
            method: "delete",
            url: common.makeAPIUrl("/sellers/" + loginId + "/orders"),
            headers: {
              Authorization: authorization,
              "Content-Type": "application/json",
            },
            data: dataList,
          };          

          axios(config)
            .then(function (response) {
              if (response.status == 200) {
                vm.$alert(common.messages.orderDeleteComplate, "", "info").then(() => { 
                  vm.setLoadingBar.loadingBarDialogShow = false;
                  vm.getDataInit();
                });
              }
            })
            .catch(function (err) {
              vm.$alert(err.response.data.message, "", "info").then(() => {});
              vm.setLoadingBar.loadingBarDialogShow = false;
            });          
        });
      }
    },
    closeOrderDetailViewDialog () {
      this.setOrderDetail.show = false
    },
    closeOrderRegistrationViewDialog () {
      this.setOrderRegistration.orderRegistrationDialogShow = false
    },
    setNewOrder() {
      const apiUrl = common.makeAPIUrl('/sellers/' + this.$session.get('loginId'));
      const headers = { 'Authorization': this.$session.get('authorization') };

      axios.get(apiUrl, { headers: headers })
        .then(response => {
          this.setOrderRegistration.sellerDetail = response.data;
          this.setOrderRegistration.orderRegistrationDialogShow = true;
          this.$EventBus.$emit("orderRegistrationDialog");
        })
        .catch(err => {
          this.handleApiError(err);
        });
    },
    handleApiError(err) {
      if (err.response && err.response.status === 401) {
        this.$alert(common.messages.login, "", "info").then(() => {
          this.$router.push("/");
        });
      } else {
        console.error("API Error:", err);
      }
    },
