      orderItem.itemName = '';
      orderItem.itemQuantity = '';
      orderItem.itemUnitValue = '';
      orderItem.itemValueCurrency = '';
      orderItem.itemWeight = '';
      orderItem.itemHSCode = '';
      orderItem.itemOriginCountryCode = 'KR';
      orderItem.itemUrl = '';
      orderItem.itemBarcode = '';
      orderItem.itemSKU = '';
      orderItem.parcelNo = '';
      this.itemIndex = this.orderDetail.orderItems.length;
      this.orderDetail.orderItems.push(orderItem)
