        if(this.orderDetail.orderItems[itemsCnt].itemOriginCountryCode == null || this.orderDetail.orderItems[itemsCnt].itemOriginCountryCode == "") { this.$alert(common.messages.itemOriginCountryCode, "", "info").then(() => { }); return false; }

