         && (this.orderDetail.orderItems[itemsCnt].itemUrl == null || this.orderDetail.orderItems[itemsCnt].itemUrl == "")) { this.$alert(common.messages.itemSalesURL, '', 'info').then(() => { }); return false; }
  
        if(this.orderDetail.orderItems[itemsCnt].itemBarcode != null && this.orderDetail.orderItems[itemsCnt].itemBarcode != "" && this.orderDetail.orderItems[itemsCnt].itemBarcode.length > 30) { this.$alert(common.messages.itemBarcode30, '', 'info').then(() => { }); return false; }
  
        if(this.orderDetail.orderItems[itemsCnt].itemSKU != null && this.orderDetail.orderItems[itemsCnt].itemSKU != "" && this.orderDetail.orderItems[itemsCnt].itemSKU.length > 255) { this.$alert(common.messages.itemSKU255, '', 'info').then(() => { }); return false; }
  
        if (this.orderDetail.orderItems[itemsCnt].parcelNo == null || this.orderDetail.orderItems[itemsCnt].parcelNo == "") { this.$alert(common.messages.itemParcelIndex, "", "info").then(() => { }); return false; }
        if (this.orderDetail.parcelCount < this.orderDetail.orderItems[itemsCnt].parcelNo) { this.$alert(common.messages.parcelCountIndex, "", "info").then(() => { }); return false; }
