          if(rows[readRowCount][39] == null || rows[readRowCount][39] == '') { this.$alert(common.messages.itemContentsType + " (Order: " + rows[readRowCount][2] + ")", '', 'info').then(() => { this.excelUploadDialog = false }); break; }
          if(rows[readRowCount][39].toUpperCase() == "GIFT" || rows[readRowCount][39].toUpperCase() == "SAMPLE" || rows[readRowCount][39].toUpperCase() == "DOCUMENT" || rows[readRowCount][39].toUpperCase() == "MERCHANDISE") { true; }
          else { this.$alert(common.messages.itemContentsTypeFilter + " (Order: " + rows[readRowCount][2] + ")", '', 'info').then(() => { this.excelUploadDialog = false }); break; }
          excelData.itemContentsType = rows[readRowCount][39];

          if(rows[readRowCount][40] == null || rows[readRowCount][40] == '') { this.$alert(common.messages.itemName + " (Order: " + rows[readRowCount][2] + ")", '', 'info').then(() => { this.excelUploadDialog = false }); break; }
          excelData.itemName = rows[readRowCount][40].toString().substring(0, 99);

          if(rows[readRowCount][41] == null || rows[readRowCount][41] == '') { this.$alert(common.messages.itemQuantity + " (Order: " + rows[readRowCount][2] + ")", '', 'info').then(() => { this.excelUploadDialog = false }); break; }
          if(!checkNumber.test(rows[readRowCount][41])) { this.$alert(common.messages.itemQuantityNumber + " (Order: " + rows[readRowCount][2] + ")", '', 'info').then(() => { this.excelUploadDialog = false }); break; }
          excelData.itemQuantity = rows[readRowCount][41];

          if(rows[readRowCount][42] == 0) { rows[readRowCount][42] = "0"; }
          if(rows[readRowCount][42] == null || rows[readRowCount][42] == '') { this.$alert(common.messages.itemUnitValue + " (Order: " + rows[readRowCount][2] + ")", '', 'info').then(() => { this.excelUploadDialog = false }); break; }
          if(!checkFloat.test(rows[readRowCount][42])) { this.$alert(common.messages.itemUnitValueFloat + " (Order: " + rows[readRowCount][2] + ")", '', 'info').then(() => { this.excelUploadDialog = false }); break; }
          excelData.itemUnitValue = rows[readRowCount][42];

          if(rows[readRowCount][43] == null || rows[readRowCount][43] == '') { this.$alert(common.messages.itemValueCurrency + " (Order: " + rows[readRowCount][2] + ")", '', 'info').then(() => { this.excelUploadDialog = false }); break; }
          checkCurrent = false          
          for(var valCurrency=0; valCurrency<currencyList.length; valCurrency++) {
            if(String(rows[readRowCount][43]).toUpperCase() == String(currencyList[valCurrency])) { checkCurrent = true }
          }
          if(!checkCurrent) { this.$alert(common.messages.itemValueCurrencyFilter + " (Order: " + rows[readRowCount][2] + ")", '', 'info').then(() => { this.excelUploadDialog = false }); break; }
          excelData.itemValueCurrency = rows[readRowCount][43].toUpperCase();

          if(rows[readRowCount][44] == null || rows[readRowCount][44] == '') { this.$alert(common.messages.itemWeight + " (Order: " + rows[readRowCount][2] + ")", '', 'info').then(() => { this.excelUploadDialog = false }); break; }
          if(!checkFloat.test(String(rows[readRowCount][44]))) { this.$alert(common.messages.itemWeightFloat + " (Order: " + rows[readRowCount][2] + ")", '', 'info').then(() => { this.excelUploadDialog = false }); break; }
          excelData.itemWeight = rows[readRowCount][44];

          if(rows[readRowCount][45] == null || rows[readRowCount][45] == '') { this.$alert(common.messages.itemHSCode + " (Order: " + rows[readRowCount][2] + ")", '', 'info').then(() => { this.excelUploadDialog = false }); break; }
          if(!checkFloat.test(String(rows[readRowCount][45]))) { this.$alert(common.messages.itemHSCodeFloat + " (Order: " + rows[readRowCount][2] + ")", '', 'info').then(() => { this.excelUploadDialog = false }); break; }
          excelData.itemHSCode = rows[readRowCount][45];

          if(rows[readRowCount][46] == null || rows[readRowCount][46] == '') { this.$alert(common.messages.itemOriginCountryCode + " (Order: " + rows[readRowCount][2] + ")", '', 'info').then(() => { this.excelUploadDialog = false }); break; }
          checkCountryCode = false
          for(var valOriginCountry=0; valOriginCountry<countryCodeList.length; valOriginCountry++) {
            if(String(rows[readRowCount][46]).toUpperCase() == String(countryCodeList[valOriginCountry])) { checkCountryCode = true }
          }
          if(!checkCountryCode) { this.$alert(common.messages.itemOriginCountryCodeFilter + " (Order: " + rows[readRowCount][2] + ")", '', 'info').then(() => { this.excelUploadDialog = false }); break; }
          excelData.itemOriginCountryCode = rows[readRowCount][46].toUpperCase();
          
          if((excelData.recipientAddressCountryCode == "RU" || excelData.recipientAddressCountryCode == "GE" || excelData.recipientAddressCountryCode == "AM")
           && (rows[readRowCount][47] == null || rows[readRowCount][47] == "")) { this.$alert(common.messages.itemSalesURL + " (Order: " + rows[readRowCount][2] + ")", '', 'info').then(() => { this.excelUploadDialog = false }); break; }
          excelData.itemUrl = rows[readRowCount][47];

          if(rows[readRowCount][48] != null && rows[readRowCount][48].length > 30) { this.$alert(common.messages.itemBarcode30 + " (Order: " + rows[readRowCount][2] + ")", '', 'info').then(() => { this.excelUploadDialog = false }); break; }
          excelData.itemBarcode = rows[readRowCount][48];

          if(rows[readRowCount][49] != null && rows[readRowCount][49].length > 255) { this.$alert(common.messages.itemSKU255 + " (Order: " + rows[readRowCount][2] + ")", '', 'info').then(() => { this.excelUploadDialog = false }); break; }
          excelData.itemSKU = rows[readRowCount][49];

          (excelData.id = 0),
            (excelData.isDeleted = "N"),
            (excelData.progress = ""),
            (excelData.sellerId = this.$session.get("loginId")),
            this.excelRowList.push(excelData);
        }
      })
      .catch(err => {
        this.$alert(err + " row : " + readRowCount + " Excel File Check", '', 'info').then(() => { this.excelUploadDialog = false })
      });
    },
  },
  mounted() {
    if (this.$session.get("loginId") == "" || this.$session.get("authorization") == "") {
      this.$alert(common.messages.login, "", "info").then(() => {
        this.$router.push("/");
      });
      return false;
    }

    $(document).prop("title", "Order Management");
    $("#bar_header").attr("style", "background-color:#E2E1F1");
    $("#top_header").show();
    $("#topNaviText").text("Order Management");

    // eslint-disable-next-line no-unused-vars
    this.fromDate = dateUtil.fromYYYYmmdd();
    this.toDate = dateUtil.toYYYYmmdd();

    // eslint-disable-next-line no-unused-vars
    var dataTableHeight = "height:" + String(window.innerHeight - 280) + "px";
    this.getDataInit();
    $("#dataTableContent").children().attr("style", dataTableHeight);
    $(".v-data-table-header>").prepend("<th style='width:15px'><input type='checkbox' style='width:20px;height:20px' id='allCheck' name='allCheck'></th>")

    this.targetAllCheck = document.querySelector('#allCheck');
    this.targetAllCheck.addEventListener('change', this.setAllCheck);
  },
  updated() {
    $(".dropdown-list").css({top:'-205px'})
    $(".v-data-table-header>tr>th").css({'background-color':'#E99EBB' });
    //$(".v-data-table-header>tr>th").removeClass("text-start");
    $(".v-data-table-header>tr>th").css({'text-align':'center' });
    $(".v-data-table-header>tr>th").css({'font-size':'15px' });
    $(".v-data-table-header>tr>th").css({'font-style':'bold' });
    
    JsBarcode(".barcode").init();
    
    $("#divBarcode").hide();
    $("#divBarcodes").hide();
    $("#btnBarcodePrint").hide();
    $("#btnBarcodesPrint").hide();
    $("#divInvoice").hide();
    $("#divInvoices").hide();
    $("#btnInvoicePrint").hide();
    $("#btnInvoicesPrint").hide();

    if (this.radioSelected == "barcode") {
      if(this.chkPrint) {
        $("#divBarcode").show();
        $("#btnBarcodePrint").show();
      } else {
        $("#divBarcodes").show();
        $("#btnBarcodesPrint").show();
      }
    }
    if (this.radioSelected == "invoice") {
      if(this.chkPrint) {
        $("#divInvoice").show();
        $("#btnInvoicePrint").show();
      } else {
        $("#divInvoices").show();
        $("#btnInvoicesPrint").show();
      }      
    }
  },
};
</script>
